<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <BasicTable
      class="fix-basic-table"
      @register="registerTable"
      @resize-column="(w, col) => (col.width = w)"
    >
      <template #toolbar>
        <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExport">{{
          t('common.action.export')
        }}</a-button>
      </template>
    </BasicTable>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { onMounted, ref } from 'vue';
  import { useMessage } from '@/hooks/web/useMessage';
  import { useI18n } from '@/hooks/web/useI18n';
  import { IconEnum } from '@/enums/appEnum';
  import { BasicTable, useTable } from '/@/components/Table';
  import { PageWrapper } from '/@/components/Page';
  import { exportExcelFile } from '@/utils/file/download';
  import { urlToPath } from '/@/utils/route';
  import { getColumns, searchForm, getList, exportExcel } from './data';
  import dayjs from 'dayjs';

  const { t } = useI18n();
  const { createConfirm, createMessage } = useMessage();
  const searchInfo = ref({});
  const { params: routeQuery } = urlToPath();

  const getParam = () => {
    const form = getForm();
    const fieldsValue = form.getFieldsValue();
    if (fieldsValue.stationIds && fieldsValue.stationIds.length > 0) {
      fieldsValue.stationId = fieldsValue.stationIds.join(',');
    }
    let param = {} as any;
    for (const key in fieldsValue) {
      if (key === 'stationIds') {
        continue;
      }
      param[key] = fieldsValue[key];
    }
    if (fieldsValue.startMonth) {
      param.startMonth = dayjs(fieldsValue.startMonth).format('YYYY-MM');
    }
    if (fieldsValue.endMonth) {
      param.endMonth = dayjs(fieldsValue.endMonth).format('YYYY-MM');
    }
    searchInfo.value = param;
    return param;
  };

  const getListByParam = async () => {
    const query = getParam();
    return await getList(query);
  };

  const tableTitle = ref<string>('工单统计');
  const [registerTable, { getForm }] = useTable({
    title: tableTitle,
    api: getListByParam,
    columns: getColumns(searchInfo),
    useSearchForm: true,
    formConfig: searchForm(),
    bordered: true,
    pagination: false,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    showIndexColumn: true,
  });

  function handleExport() {
    createConfirm({
      title: t('common.message.confirmTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        const query = getParam();
        query.filename = `${tableTitle.value}.xls`;
        await exportExcel(query).then((res) => {
          exportExcelFile(res?.data, query.filename);
        });
        query.filename = null;
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }

  onMounted(() => {
    const form = getForm();
    const params = { ...routeQuery };
    if (params.startDate) {
      params.startMonth = routeQuery.startDate;
    }
    if (params.endDate) {
      params.endMonth = routeQuery.endDate;
    }
    if (params.stationIds) {
      params.stationIds = params.stationIds.split(',');
    } else {
      params.stationIds = [];
    }
    form.setFieldsValue(params);
  });
</script>
<style lang="less" scoped>
  @import '/@/design/biz/bizList.less';
</style>
