import { defHttp } from '/@/utils/http/axios';
import { reactive } from 'vue';
import { BasicColumn, useRender } from '/@/components/Table';
import type { FormProps } from '/@/components/Form/src/types/form';
import dayjs from 'dayjs';
import { useUserStore } from '/@/store/modules/user';
import * as CommonApi from '/@/api/baseset/common';

export enum Api {
  LIST_URL = '/bi/maintenance/workorder/list',
  EXPORT_EXCEL_URL = '/bi/maintenance/workorder/export-excel',
  LIST_ORG_URL = '/bi/common/org/info',
}

// 获得工单统计列表
export const getList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_URL,
      params,
    },
    { isOnlyResult: true },
  );

// 导出工单统计列表
export const exportExcel = (params) =>
  defHttp.download(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
    },
    { isReturnNativeResponse: true },
  );

// 获得组织列表
export const getOrgList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_ORG_URL,
      params,
    },
    { isOnlyResult: true },
  );

const customLinkRender = (text, column, record, searchInfo) => {
  if (!record.stationOrganId) {
    return text;
  }
  const param = { status: '0,1,2' } as any;
  if (record.stationId === 'subtotal') {
    param.stationOrganId = record.stationOrganId;
  } else {
    param.stationId = record.stationId;
  }
  const workOrderTypes = ['qx', 'yh', 'cc', 'zjqx', 'lsjg', 'ff'];
  const resultTypes = ['resolved', 'unresolved'];
  if (workOrderTypes.includes(column.dataIndex)) {
    param.workOrderType = column.dataIndex;
  } else if (resultTypes.includes(column.dataIndex)) {
    const result = column.dataIndex === 'resolved' ? '1' : '0';
    param.result = result;
  } else if (column.dataIndex === 'overdue') {
    param.overdue = '1';
  }
  const info = searchInfo.value;
  if (info.dateType === 'year' && info.year) {
    param.startMonth = `${info.year}-01`;
    param.endMonth = `${info.year}-12`;
  } else if (info.dateType === 'month' && info.startMonth && info.endMonth) {
    param.startMonth = info.startMonth;
    param.endMonth = info.endMonth;
  }
  const url = '/po/workorder/index';
  return useRender.renderIframeLink(url, undefined, param, text);
};

