const currentYear = String(dayjs().year());
export const searchForm = (): FormProps => {
  const stationParams = reactive({ organId: undefined });
  return {
    baseColProps: { span: 8 },
    labelWidth: 120,
    autoAdvancedLine: 1,
    schemas: [
      {
        field: 'dateType',
        label: '时间维度',
        component: 'Select',
        defaultValue: 'year',
        componentProps: ({ formModel }) => {
          return {
            options: [
              { value: 'year', label: '年度' },
              { value: 'month', label: '月份' },
            ],
            onChange: (value) => {
              if (value === 'year') {
                formModel['[startMonth, endMonth]'] = [];
              } else if (value === 'month') {
                formModel.year = '';
              } else {
                formModel.year = '';
                formModel['[startMonth, endMonth]'] = [];
              }
            },
          };
        },
      },
      {
        field: 'year',
        label: '创建时间',
        component: 'DatePicker',
        defaultValue: currentYear,
        componentProps: {
          format: 'YYYY',
          valueFormat: 'YYYY',
          picker: 'year',
          style: 'width: 100%',
        },
        show: ({ values }) => {
          return values.dateType === 'year';
        },
      },
      {
        field: '[startMonth, endMonth]',
        label: '创建时间',
        component: 'RangePicker',
        componentProps: {
          format: 'YYYY-MM',
          valueFormat: 'YYYY-MM',
          picker: 'month',
          placeholder: ['开始月份', '结束月份'],
          style: 'width: 100%',
        },
        show: ({ values }) => {
          return values.dateType === 'month';
        },
      },
      {
        field: 'stationOrganId',
        label: '所属公司',
        component: 'ApiSelect',
        componentProps: ({ formModel }) => {
          return {
            api: async () => {
              const data = await getOrgList({});
              const options: any = [];
              if (data && data.length > 0) {
                data.forEach((item) => {
                  options.push({ value: item.id, label: item.label });
                });
              }
              return options;
            },
            onChange: (value) => {
              stationParams.organId = value;
              formModel.stationIds = [];
            },
          };
        },
        show: useUserStore().isMultiOrganization,
      },
      {
        field: 'stationIds',
        label: '电站名称',
        component: 'ApiSelect',
        componentProps: {
          api: async () => {
            const data = await CommonApi.getStationList({ ...stationParams });
            const options: any = [];
            if (data && data.length > 0) {
              data.forEach((item) => {
                options.push({ value: item.id, label: item.name });
              });
            }
            return options;
          },
          mode: 'multiple',
          maxTagCount: 'responsive',
          filterOption: (input: string, option: any) => {
            return option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0;
          },
        },
      },
    ],
  };
};
